import React from "react";
import { Redirect, Route, useHistory } from "react-router-dom";
import { toast } from "react-toastify";
import AdminServices from "services/api/AdminServices";
import db from "../../../../../libs/db";
import BottomNavBar from "../../atoms/BottomNavBar";

function ProtectedRoutes({ component: Component, bottom_tab = true, ...restOfProps }) {
  const isAuthenticated = localStorage.getItem("user");
  var history = useHistory()
  const loadData = async () => {
    let profiled = false;
    try {
      if (isAuthenticated) {
        db.get(db.model.user).then(user=>{
          db.get(db.model.clientData).then(async (p) => {
            if (p === null) {
              let res = await AdminServices.getClient(user.userData.id).then(res => res);
              if (res.type === true) {
                const a = JSON.parse(res.data);
                let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
                console.log("Get Client API :", aa)
                profiled = aa.Client[0].IsProfileComplete === 1
                if (profiled && restOfProps.path.includes("activate-direct-mf")) {
                  history.push("/dashboard")
                }
                await db.set(db.model.clientData, JSON.stringify(aa))
              }
            } else {
              profiled = p.Client[0].IsProfileComplete === 1
              if (profiled && restOfProps.path.includes("activate-direct-mf")) {
                history.push("/dashboard")
              }
            }
          });
        })
      }
    } catch (error) {
      toast.error("Try again ...");

    }
  };

  React.useEffect(() => {
    loadData();
  }, []);

  return (
    <Route
      {...restOfProps}
      render={(props) =>
        isAuthenticated ?
          <>
            {bottom_tab ? <BottomNavBar />
              : null}

            <Component {...props} />
          </>
          : <Redirect to="/signup" />
      }
    />
  );
}

export default ProtectedRoutes;
