import { baseImgURL } from 'assets/Constant'
import React, { useEffect, useState } from 'react'
import { FiBriefcase } from 'react-icons/fi'
import { HiOutlineHome, HiOutlineMenu } from 'react-icons/hi'
import { IoGiftOutline } from 'react-icons/io5'
import { useHistory, useLocation } from 'react-router-dom'

const BottomNavBar = (props) => {
    const history = useHistory()
    const location = useLocation();
    const [activeTabs, setActiveTabs] = useState('home')

    useEffect(() => {
        if (location.pathname.includes("dashboard")) {
            setActiveTabs('home')
        } else if (location.pathname.includes("portfolio")) {
            setActiveTabs('portfolio')
        } else if (location.pathname.includes("invest")) {
            setActiveTabs('invest')
        } else if (location.pathname.includes("rewards")) {
            setActiveTabs('rewards')
        } else if (location.pathname.includes("menu")) {
            setActiveTabs('menu')
        }
    })

    return (
        <div className='bottom-nav'>
            <div className='bn-tab' onClick={() => {history.push('/dashboard')}}>
                <HiOutlineHome size={20} className={activeTabs === 'home' ? 'footerIconActive' : 'footerIcon'} />
                <div className={activeTabs==='home'?'footerTextActive':'footerText'}>Home</div>
            </div>
            <div className='bn-tab' onClick={() => {history.push('/portfolio')}}>
                <FiBriefcase size={20} className={activeTabs === 'portfolio' ? 'footerIconActive' : 'footerIcon'} />
                <div className={activeTabs==='portfolio'?'footerTextActive':'footerText'}>Portfolio</div>
            </div>
            <div className='bn-tab' onClick={() => {history.push('/Invest')}}>
                <img src={baseImgURL+"mintbox_bottom_nav.svg"} height={50} width={50} style={{ marginTop: "-25px" }} />
                <div className={activeTabs==='invest'?'footerTextActive':'footerText'}>Invest</div>
            </div>
            <div className='bn-tab' onClick={() => {history.push('/menu/rewards')}}>
                <IoGiftOutline size={20} className={activeTabs === 'rewards' ? 'footerIconActive' : 'footerIcon'} />
                <div className={activeTabs==='rewards'?'footerTextActive':'footerText'}>Rewards</div>
            </div>
            <div className='bn-tab' onClick={() => {history.push('/menu')}}>
                <HiOutlineMenu size={20} className={activeTabs === 'menu' ? 'footerIconActive' : 'footerIcon'} />
                <div className={activeTabs==='menu'?'footerTextActive':'footerText'}>Menu</div>
            </div>
        </div>
    )
}

export default React.memo(BottomNavBar);